import { ExpandLess, ExpandMore } from '@mui/icons-material'
import { Collapse, Divider, ListItemButton, ListItemIcon, ListItemText } from '@mui/material'
import React from 'react'
import { useDispatch } from 'react-redux'
import { useNavigate } from 'react-router'
import useCheckMobileScreen from '../../hooks/useCheckMobileScreen'
import { uiHideSideBar } from '../../redux/actions/ui.actions'
import './sidebar.css'

export const SidebarItem = ({ item, nivel }) => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const isMobile = useCheckMobileScreen();

    const { url, name, items, icon } = item
    const [collapsed, setCollapsed] = React.useState(false);
    const [isSelected, setSelected] = React.useState(false)
    const handleCollapse = () => {

        if (items.length > 0) {
            setCollapsed(prevValue => !prevValue)
            setSelected(prevValue => !prevValue);
        }
        else {
            navigate(url);
            if (isMobile) {
                dispatch(uiHideSideBar())
            }
        }
    }
    return (
        <div >
            <Divider />
            <ListItemButton onClick={() => handleCollapse()} key={item.id} style={{ paddingLeft: nivel + 10 }}>
                <ListItemIcon style={{ width: '10px !important' }}>
                    {icon !== '' && icon}
                </ListItemIcon>
                <ListItemText primary={name} />
                {
                    (items.length > 0 && !isSelected) && <ExpandMore />
                }
                {
                    (items.length > 0 && isSelected) && <ExpandLess />
                }
            </ListItemButton>
            <Collapse in={collapsed} timeout="auto" unmountOnExit>
                {
                    items &&
                    items.map((el, ix) => (
                        <SidebarItem nivel={nivel * 2} item={el} key={ix} />
                    ))
                }
            </Collapse>
        </div>
    )
}
