import { uiConstants } from "../constants/ui.constanst"

const { uiOpenModal, uiCloseModal, uiOpenLoading, uiCloseLoading,
     uiHideSideBar,uiShowSideBar,uiOptionSelected,uiErrorMessage,
     uiShowMenuUser,uiHideMenuUser,uiShowQuickSearch,uiHideQuickSearch } = uiConstants;

const initialState = {
    modalOpen: false,
    loading: false,
    sideBar: false,
    optionSelected:{},
    menuUser:false,
    quickSearch:false,
    error:null,
}
export const uiReducer = (state = initialState, action) => {

    switch (action.type) {
        case uiOpenModal:
            return {
                ...state,
                modalOpen: true
            }
        case uiCloseModal:
            return {
                ...state,
                modalOpen: false
            }
        case uiOpenLoading:
            return {
                ...state,
                loading: true
            }

        case uiCloseLoading:
            return {
                ...state,
                loading: false
            }
        case uiHideSideBar:
            return {
                ...state,
                sideBar: false
            }
        case uiShowSideBar:
            return {
                ...state,
                sideBar: true
            }
        case uiShowMenuUser:
            return {
                menuUser:true
            }    
        case uiHideMenuUser:
            return {
                menuUser: false
            }
        case uiShowQuickSearch:
            return {
                ...state,
                quickSearch:true
            }
        case uiHideQuickSearch:
            return {
                ...state,
                quickSearch:false
            }    
        case uiErrorMessage:
            return {
                ...state,
                error:action.payload
            }
        default:
            return state;
    }
}