import styled from '@emotion/styled';
import { Box, Card, circularProgressClasses, Container, CssBaseline, Paper } from '@mui/material';
import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { uiHideSideBar, uiShowSideBar } from '../../redux/actions/ui.actions';
import { DashboardRouter } from '../../Routers/DashboardRouter';
import { AppBarCustom } from '../appbar/AppBarCustom';
import { Footer } from '../footers/Footer';
import Sidebar from '../sidebar/Sidebar';



const drawerWidth = 260;

const Main = styled('main', { shouldForwardProp: (prop) => prop !== 'open' })(
    ({ theme, open }) => ({
        flexGrow: 1,
        padding: theme.spacing(3),
        transition: theme.transitions.create('margin', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        marginLeft: `-${drawerWidth}px`,
        ...(open && {
            transition: theme.transitions.create('margin', {
                easing: theme.transitions.easing.easeOut,
                duration: theme.transitions.duration.enteringScreen,
            }),
            marginLeft: 0,
        }),
    }),
);


const DrawerHeader = styled('div')(({ theme }) => ({
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
}));

export const Dashboard = () => {
    const { sideBar } = useSelector(state => state.ui);

    return (
        <div>
            <Box style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <CssBaseline />
                <AppBarCustom appName="GoCredix" />
                <Sidebar drawerWidth={drawerWidth} />
                <Main open={sideBar}>
                    <DrawerHeader />
                    <Container component="main" maxWidth="lg" sx={{ mb: 8 }}>
                        <Paper  elevation={4} sx={{ my: { xs: 3, md: 1 }, p: { xs: 2, md: 3 } }}>
                            <DashboardRouter />
                        </Paper>
                    </Container>
                </Main>
                <Footer />
            </Box>
        </div>
    )
}
